import axios from 'axios';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import { showLoader, hideLoader } from './helpers.js';
// API
export const BASE_URL = 'https://furniture-store.b.goit.study/api';

// –ï–Ω–¥–ø–æ—ñ–Ω—Ç–∏
export const ENDPOINTS = {
  FURNITURES: '/furnitures',
  CATEGORIES: '/categories',
  ORDERS: '/orders',
  FEEDBACKS: '/feedbacks',
  POPULARPRD: '/furnitures?type=popular',
};

// axios
const api = axios.create({
  baseURL: BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// –º–µ–±–ª—ñ
export const fetchFurnitures = async page => {
  showLoader();
  try {
    const response = await api.get(ENDPOINTS.FURNITURES, {
      params: {
        page,
        limit: 8,
      },
    });
    return response.data;
  } catch (error) {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –º–µ–±–ª—ñ. –°–ø—Ä–æ–±—É–π—Ç–µ –ø—ñ–∑–Ω—ñ—à–µ.',
      position: 'topRight',
      timeout: 4000,
    });
    return null;
  } finally {
    hideLoader();
  }
};
